
import { defineComponent } from "vue";
import WordClock from "./components/WordClock.vue";

export default defineComponent({
  name: "App",
  components: {
    WordClock,
  },
  mounted() {
    setInterval(() => this.$forceUpdate(), 1000)
  },
  methods: {
    getHours() {
      return new Date().getHours();
    },
    getMinutes() {
      return new Date().getMinutes();
    }
  }
});
